import React, { useState, useEffect, useRef } from "react";
import "./Users.css";
import { Link } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faCheck,
  faTimes,
  faInfoCircle,
} from "@fortawesome/free-solid-svg-icons";

const USER_REGEX = /^[A-z][A-z0-9-_]{3,23}$/;
const PWD_REGEX = /^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[!@#$%]).{8,24}$/;

const API_BASE_URL = "https://frontend.internetskimarketing.eu/backend/wp-json";

const SignUp = () => {
  const userRef = useRef();
  const errRef = useRef();

  const [user, setUser] = useState("");
  const [validName, setValidName] = useState(false);
  const [userFocus, setUserFocus] = useState(false);

  const [pwd, setPwd] = useState("");
  const [validPwd, setValidPwd] = useState(false);
  const [pwdFocus, setPwdFocus] = useState(false);

  const [matchPwd, setMatchPwd] = useState("");
  const [validMatch, setValidMatch] = useState(false);
  const [matchFocus, setMatchFocus] = useState(false);

  const [errMsg, setErrMsg] = useState("");
  const [success, setSuccess] = useState(false);

  const [email, setEmail] = useState("");

  useEffect(() => {
    userRef.current.focus();
  }, []);

  useEffect(() => {
    setValidName(USER_REGEX.test(user));
  }, [user]);

  useEffect(() => {
    setValidPwd(PWD_REGEX.test(pwd));
    setValidMatch(pwd === matchPwd);
  }, [pwd, matchPwd]);

  useEffect(() => {
    setErrMsg("");
  }, [user, pwd, matchPwd]);

  const handleSubmit = async (e) => {
    e.preventDefault();

    if (!validName || !validPwd || !validMatch) {
      setErrMsg("Invalid Entry");
      return;
    }

    try {
      // 1️⃣ Authenticate as Admin to Get JWT Token
      const authResponse = await fetch(`${API_BASE_URL}/jwt-auth/v1/token`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          username: "dgaljar@frontend.internetskimarketing.eu",
          password: "sNjXY$ucMwX3uYV*s0jA9)Lb",
        }),
      });

      if (!authResponse.ok) {
        throw new Error("Admin authentication failed.");
      }

      const authData = await authResponse.json();
      const token = authData.token;

      console.log("Admin JWT Token:", token);

      // 2️⃣ Register the New User with the Token
      const registerResponse = await fetch(`${API_BASE_URL}/wp/v2/users`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${token}`, // Send token in Authorization header
        },
        body: JSON.stringify({
          username: user,
          password: pwd,
          email: `${user}@example.com`, // Adjust email logic as needed
          roles: ["subscriber"],
        }),
      });

      if (!registerResponse.ok) {
        throw new Error("User registration failed.");
      }

      const registerData = await registerResponse.json();
      console.log("User Registered:", registerData);

      setSuccess(true);
    } catch (err) {
      console.error("Error:", err);
      setErrMsg(err.message);
      errRef.current.focus();
    }
  };
  return (
    <>
      <div className="container-fluid">
        <div className="row">
          <div className="d-none d-md-flex col-md-6 login-intro">
            <Link to="/" className="me-md-auto ">
              <img src="img/Logo.png" alt="logo" />
            </Link>

            <div className=" text-center text-md-start">
              <h1>Welcome to MetaBlog!</h1>
              <p>
                Lorem ipsum dolor sit amet, consectetur adipiscing elit.
                Vulputate ut laoreet velit ma.
              </p>
            </div>
            <img src="img/bgimg.svg" alt="" />
          </div>

          <div className="col-12 col-md-6 login-form justify-content-center px-4 px-md-0">
            <div className="mt-4 mt-md-0">
              <h2>Create your account</h2>
              <p>It's free and eays</p>
              {success ? (
                <div className="py-5 my-5 text-center">
                  <h1 className="text-success fs-1 fw-bold">Success!</h1>
                  <Link to="/signin" className="text-decoration-underline">Sign in here.</Link>
                </div>
              ) : (
                <>
                  <p
                    ref={errRef}
                    className={errMsg ? "errmsg" : "offscreen"}
                    aria-live="assertive"
                  >
                    {errMsg}
                  </p>
                  <form onSubmit={handleSubmit}>
                    <label htmlFor="username">
                      Username:
                      <FontAwesomeIcon
                        icon={faCheck}
                        className={validName ? "valid" : "hide"}
                      />
                      <FontAwesomeIcon
                        icon={faTimes}
                        className={validName || !user ? "hide" : "invalid"}
                      />
                    </label>
                    <input
                      type="text"
                      placeholder="Enter your username"
                      id="username"
                      ref={userRef}
                      autoComplete="off"
                      onChange={(e) => setUser(e.target.value)}
                      value={user}
                      required
                      aria-invalid={validName ? "false" : "true"}
                      aria-describedby="uidnote"
                      onFocus={() => setUserFocus(true)}
                      onBlur={() => setUserFocus(false)}
                      className="mb-4"
                    />
                    <p
                      id="uidnote"
                      className={
                        userFocus && user && !validName
                          ? "instructions"
                          : "offscreen"
                      }
                    >
                      <FontAwesomeIcon icon={faInfoCircle} />
                      4 to 24 characters.
                      <br />
                      Must begin with a letter.
                      <br />
                      Letters, numbers, underscores, hyphens allowed.
                    </p>
                    <label>E-mail or phone number</label>
                    <input
                      type="text"
                      placeholder="Enter your e-mail or phone number"
                      value={email}
                      onChange={(e) => setEmail(e.target.value)}
                      className="mb-4"
                      required
                    />

                    <label htmlFor="password">
                      Password:
                      <FontAwesomeIcon
                        icon={faCheck}
                        className={validPwd ? "valid" : "hide"}
                      />
                      <FontAwesomeIcon
                        icon={faTimes}
                        className={validPwd || !pwd ? "hide" : "invalid"}
                      />
                    </label>
                    <input
                      type="password"
                      id="password"
                      onChange={(e) => setPwd(e.target.value)}
                      value={pwd}
                      required
                      aria-invalid={validPwd ? "false" : "true"}
                      aria-describedby="pwdnote"
                      onFocus={() => setPwdFocus(true)}
                      onBlur={() => setPwdFocus(false)}
                      placeholder="Enter your password"
                    />
                    <p
                      id="pwdnote"
                      className={
                        pwdFocus && !validPwd ? "instructions" : "offscreen"
                      }
                    >
                      <FontAwesomeIcon icon={faInfoCircle} />
                      8 to 24 characters.
                      <br />
                      Must include uppercase and lowercase letters, a number and
                      a special character.
                      <br />
                      Allowed special characters: ! @ # $ %
                    </p>
                    <label htmlFor="confirm_pwd">
                      Confirm Password:
                      <FontAwesomeIcon
                        icon={faCheck}
                        className={validMatch && matchPwd ? "valid" : "hide"}
                      />
                      <FontAwesomeIcon
                        icon={faTimes}
                        className={validMatch || !matchPwd ? "hide" : "invalid"}
                      />
                    </label>
                    <input
                      type="password"
                      id="confirm_pwd"
                      onChange={(e) => setMatchPwd(e.target.value)}
                      value={matchPwd}
                      required
                      aria-invalid={validMatch ? "false" : "true"}
                      aria-describedby="confirmnote"
                      onFocus={() => setMatchFocus(true)}
                      onBlur={() => setMatchFocus(false)}
                      placeholder="Confirm your password"
                    />
                    <p
                      id="confirmnote"
                      className={
                        matchFocus && !validMatch ? "instructions" : "offscreen"
                      }
                    >
                      <FontAwesomeIcon icon={faInfoCircle} />
                      Must match the first password input field.
                    </p>

                    <label for="" className="d-flex align-items-start terms">
                      <input type="checkbox" className="checkbox" />
                      <span>
                        By creating an account means you agree to the{" "}
                        <Link to="/" className="terms-policy">
                          Terms and Conditions
                        </Link>
                        , and our{" "}
                        <Link to="/" className="terms-policy">
                          Privacy Policy
                        </Link>
                      </span>
                    </label>

                    <button disabled={!validName || !validPwd || !validMatch}>
                      Sign Up
                    </button>
                  </form>
                </>
              )}

              <p className="seperator">or do it via other accounts</p>

              <ul>
                <li>
                  <Link to="">
                    <img src="img/google.svg" alt="google" />
                  </Link>
                </li>
                <li>
                  <Link to="/">
                    <img src="img/apple.svg" alt="apple" />
                  </Link>
                </li>
                <li>
                  <Link to="/">
                    <img src="img/fb.svg" alt="facebook" />
                  </Link>
                </li>
              </ul>
              <p className="text-center">
                Already have an account? <Link to="/signin">Sign In</Link>
              </p>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default SignUp;
